.form-group.layout_box .form-inline label input {
  display: none;
}

.form-group.layout_box .form-inline.font-size label {
  color: #000000;
  width: auto;
  border: none;
  box-shadow: none;
}

.form-group.layout_box .form-inline.font-size label small {
  font-weight: normal;
  padding-left: 1em;
}

.form-group.layout_box .form-inline.font-size label input {
  display: inline-block;
}

.form-group .btn_cont {
  width: 100%;
  text-align: left;
}

.form-group .btn-group {
  position: absolute;
  top: 1em;
  right: 1em;
}

.form-group.select {
  display: none;
}

.admin_head {
  width: 100%;
  padding: 1em;
  padding-left: 1.5em;
  border: 1px solid #e5e5e5;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.04);
  background: #fff;
  position: relative;
}

.admin_head .small {
  font-weight: normal;
  font-size: 90%;
}

.admin_head::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 5px;
  height: 100%;
  background-color: #00a0d2;
}

.parts-inline {
  display: flex;
  align-items: center;
  margin-right: 1em;
}
